<template>

  <div id="app">
  <nav class="navbar navbar-default">
  <div class="container-fluid">
    <div class="navbar-header">
     
      <a class="navbar-brand" href="#">Board <small>BETA</small></a>

    </div>

    </div>

  </nav>

    <div class="container">
          <button v-if="!user.email" class="btn-primary btn btn-sm" @click="login()">登录</button>
       <div v-html="topic['.value']"></div>
      <p>Email:{{ user.email }}</p>

    <div class="row">
      
      <div class="col-md-12">
      <div class="form-group">
                     <div id="editor" class="form-control" style="width:100%;" @keyup="changeMessage">{{code.code}}</div>

      </div>
        </div>
    </div>
    <div style="position:relative;"></div>
<div class="row"> 
<div class="col-md-6" v-for="code in codes">
    <div class="panel panel-default">
      <div class="panel-heading">      {{code.email}}</div>
      <div class="panel-body">
          <pre>{{ (code.code) }} </pre>
      </div>
    </div>
</div>
</div>


   
  

    </div>
  </div>
</template>

<script>
import Hello from './components/Hello'
import firebase from 'firebase'
import db from './db'
var editor
var first = true
export default {
  name: 'app',
  beforeCreate: function() {

    console.log(111222)
      // Setup Firebase onAuthStateChanged handler
      //
      // https://firebase.google.com/docs/reference/js/firebase.auth.Auth
      // https://firebase.google.com/docs/reference/js/firebase.auth.Auth#onAuthStateChanged
      firebase.auth().onAuthStateChanged(function(user) {

        if (user) {
        this.user = user
        this.$bindAsArray('codes', db.ref('codes/'))
        this.$bindAsObject('code', db.ref('codes/' +  user.uid))
        this.$firebaseRefs.code.update({'email':user.email})
        this.$bindAsObject('topic', db.ref('config/topic' ))
          // https://github.com/vuejs/vuefire/blob/master/src/vuefire.js#L169
        }

      }.bind(this))
    },
    mounted(){
          editor = ace.edit("editor")

          var JavaMode = ace.require("ace/mode/java").Mode;
          editor.session.setMode(new JavaMode());
          editor.$blockScrolling = Infinity 

    },
  components: {
    Hello
  },
  watch:{
    'code.code':(newV,oldV)=>{
      if (first){
        editor.setValue(newV,1)
        first = false
      }

    }
  },
  methods: {
    nl2br(p){
      return p.replace("\n",'<br>')
    },
    changeMessage(e){
      console.log(editor.getValue())
              this.$firebaseRefs.code.update({'code':editor.getValue()})
     },
    login(){
      var provider = new firebase.auth.GoogleAuthProvider();
      var self = this
      firebase.auth().signInWithPopup(provider).then(function(result) {
        console.log(result)
        var token = result.credential.accessToken
        self.user = result.user
  // ...
    }).catch(function(error) {
      // Handle Errors here.
      var errorCode = error.code;
      var errorMessage = error.message;
      // The email of the user's account used.
      var email = error.email;
      // The firebase.auth.AuthCredential type that was used.
      var credential = error.credential;
      // ...
    })
    }
  },

  data() {
    return {  
        user: {},
        code:{},
        codes: [],
        topic:{},
      }
  }
}
</script>

<style>
 @import url('https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap.min.css');
 @import url('https://maxcdn.bootstrapcdn.com/bootswatch/3.3.7/cosmo/bootstrap.min.css');
   #editor {
       
        width: 500px;
        height: 400px;
    }

</style>



